import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/box/Box.scss';

/* 
	content
*/
class Box extends React.Component {

	render(){
		var { box, sides, dispatch } = this.props;
		// console.log('%c box',"font-weight:bold;color:green;",box);
		if (!box) {
			throw new Error("Box component must be supplied with box argument");
		}
		box.content = box.content || this.props.children;
		if (!box.content) {
			throw new Error("Box \""+box.title+"\" must be supplied with props.children or box.content");
		}

		var Sides = [];
		if (sides) {
			for (let b=0; b<sides.length; b++) {
				Sides.push(
					<div key={b} className="navItem link" onClick={()=>{ if (sides[b].content) { dispatch( layoutActions.sideBox(sides[b]) ) } }}>
						{sides[b].icon}
					</div>
				);
			}
		}

		return(
			<div ref="box" className={"box Box " + ( box.theme ? " theme_"+box.theme : "") + ( box.className ? " "+box.className : "" ) + ( box.opened ? " in_modal" : " in_page" ) } id={box.id || ""}>
				<div className="box_content">

					<div className="box_top">
						<div className="navItems">
							<div className="navItem navTitle">
								<span className="navItemTitle">
									{box.icon && box.icon}
									{box.title && box.title}
								</span>
							</div>
							{Sides && Sides}
						</div>
					</div>

					<div className="box_middle">
						{box.content && box.content}
					</div>

				</div>
			</div>
		);

	}
};

const mapStateToProps = (state) => {
    return {};
};

export default connect(mapStateToProps)( Box );


