import { createStore, applyMiddleware } from 'redux';
import createLogger from 'redux-logger';
import createReducer from '@src/reducers';
import { rpcdMiddleware } from '@src/middleware/rpcd';

/*
	thunk
	we don't need this right now, and possibly will never, so the two lines commented out until needed
	packages.json - were using "redux-thunk": "^2.2.0", which is also deleted now
*/
// import thunkMiddleware from 'redux-thunk';

const configureStore = (history, preloadedState = {}) => {
	const middleware = [
		// thunkMiddleware,
		createLogger({
			predicate: (getState, action) => { return (action.type.indexOf("@@redux") === -1 && action.type.indexOf("/submitting") === -1); },
			collapsed: true
		}),
		rpcdMiddleware
	];

	return createStore(
		createReducer(),
		preloadedState,
		applyMiddleware(...middleware)
	);
};

export default configureStore;


