import { combineReducers } from 'redux';

import user from '@src/states/user/reducer';
import layout from '@src/states/layout/reducer';
import switches from '@src/states/switches/reducer';

const createReducer = () => {
    return combineReducers({
        switches: switches,
        layout: layout,
        user: user,
    })
};

export default createReducer;


