/*
	app
*/
import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, Redirect, browserHistory } from 'react-router';
import { Provider } from 'react-redux';


/*
	log
*/
if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
	window.log = function(){
		let args = Array.from(arguments);
		/*
			theme
		*/
		var log = {
			titleColor: "rgba(255,255,255,0.75)",
			textColor: "rgba(0,0,0,0.75)",
			bgColor: "rgb(30,30,30)"
		}
		switch (args[0]) {
			case "action success":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "green";
				log.textColor = "darkgreen";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "action fail":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "red";
				log.textColor = "darkred";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "action":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "darkgrey";
				log.textColor = "black";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "error":
				log.titleColor = "Tomato";
				log.textColor = "red";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "warning":
				log.titleColor = "lightorange";
				log.textColor = "darkorange";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "success":
				log.titleColor = "lightgreen";
				log.textColor = "darkgreen";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "log":
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			default:
			break;
		}
		/*
			log
		*/
		for (var i = 0; i < args.length; i++) {
			if (i===0) {
				if (typeof args[0]==="string") {
					if (args[1]) {
						// groupStart
						window.console.groupCollapsed("%c " + args[i] + " ", 'background:'+log.bgColor+';color:'+log.titleColor+';padding:1px 0;');
					} else {
						// log
						console.log("%c " + args[i] + " ", 'background:'+log.bgColor+';color:'+log.titleColor+';padding:1px 0;');
					}
				}
			} else if (args[i]) {
				if (args[i].length>1 && (typeof args[0]==="string") && (typeof args[1]==="object")) {
					// group (mimick redux logger "prev state" "action" "next state" logging)
					console.log("%c " + args[i][0], "color:darkgrey;", args[i][1]);
				} else {
					// normal
					console.log(args[i]);
				}
			}
		}
		// groupEnd
		if (typeof args[0]==="string" && args[1]) {
			window.console.groupEnd();
		}
	}
} else {
	window.log = function(){
	};
}


/*
	store
	middleware
*/
import configureStore from '@src/store';
const store = configureStore(browserHistory);


/*
	style
*/
import '@src/style/index.scss';
import '@src/components/form/LuxulForm.scss';


/*
	route
	dynamic from window.routes, also some manual routes such as "login" and "page not found"
*/
import * as layoutActions from '@src/states/layout/actions';
const routeOnEnter = ()=>{
	store.dispatch(layoutActions.message({}));
};
const Routes = [];
var routeComponent;

// default route
for (let i = 0; i < window.routes.length; i++) {
	if (window.device.supportsComponent(i, window.routes)) {
		if (!window.defaultRoute) {
			window.defaultRoute = window.routes[i];
			Routes.push(<Redirect key="routeRedirect" from="/" to={window.defaultRoute.url} />);
			break;
		}
	}
}

// login route
routeComponent = require('./pages/'+window.routes.login.component);
if (routeComponent.default) {
	Routes.push(<Route key="routeLogin" path={window.routes.login.url} component={routeComponent.default} page={window.routes.login} onEnter={routeOnEnter} />);
}

// modules
for (let i = 0; i < window.routes.length; i++) {
	if (window.device.supportsComponent(i, window.routes)) {
		// 1st level nav/url
		// /example: quicksetup
		Routes.push(<Route key={"route"+i} path={window.routes[i].url} component={function(i){ return require('./pages/'+window.routes[i].component).default; }(i)} page={window.routes[i]} onEnter={routeOnEnter} />);
		if (window.routes[i].children) {
			for (let ii = 0; ii < window.routes[i].children.length; ii++) {
				if (window.device.supportsComponent(ii, window.routes[i].children)) {
					// 2nd level nav/url routes
					// example: /wifi/connect
					Routes.push(<Route key={"route"+i+"."+ii} path={window.routes[i].children[ii].url} component={function(i, ii){ return require('./pages/'+window.routes[i].children[ii].component).default; }(i, ii)} page={window.routes[i].children[ii]} onEnter={routeOnEnter} />);
					if (window.routes[i].children[ii].children) {
						for (let iii = 0; iii < window.routes[i].children[ii].children.length; iii++) {
							if (window.device.supportsComponent(iii, window.routes[i].children[ii].children)) {
								// 3rd level nav/url routes
								// example: /network/multiwan/options
								Routes.push(<Route key={"route"+i+"."+ii} path={window.routes[i].children[ii].children[iii].url} component={function(i, ii, iii){ return require('./pages/'+window.routes[i].children[ii].children[iii].component).default; }(i, ii, iii)} page={window.routes[i].children[ii].children[iii]} onEnter={routeOnEnter} />);
							}
						}
					}
				}
			}
		}
	}
}

// generic modules, to be used for all devices always
routeComponent = require('./pages/'+window.routes.pageNotFound.component);
if (routeComponent.default) {
	Routes.push(<Route key="routepageNotFound" path={window.routes.pageNotFound.url} component={routeComponent.default} page={window.routes.pageNotFound} onEnter={routeOnEnter} />);
}


/*
	go
*/
// injectTapEventPlugin();
ReactDOM.render(
	<Provider store={store}>
		<Router history={browserHistory}>
			{Routes}
		</Router>
	</Provider>,
	document.getElementById('app')
);








