import React from 'react';
import { connect } from 'react-redux';

import { Link } from 'react-router';
// import * as layoutActions from '@src/components/layout/actions';

import '@src/components/layout/Bottom.scss';

class Bottom extends React.PureComponent {

	render() {
		const { user } = this.props;

		return (
			<div className="bottom_container">
			<div id="Bottom" className="bottom">

				<div className="navItems navHeading">

					<span className="navItem navLogo nav_left link">
						<img className="" src="/branding/logo/legrand_onDark3.svg" alt="Legrand" />
					</span>

					<span className="navItem nav_center">
						<span className="navItemTitle">Copyright &copy;2017 Luxul</span>
					</span>

					{ user.loggedIn 
					? 
					<span className="navItem nav_right link">
						<span className="navItemTitle"><Link href="/login"><span>logout <span className="fontIcon icon-pk-lock" /></span></Link></span>
					</span>
					:
					""
					}

				</div>
				
			</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	user: state.user
    };
};
export default connect(mapStateToProps)(Bottom);


