import $ from "jquery";

import React from 'react';

class Pages extends React.PureComponent {

	/*
		scrollHorizontally
		".scrollHorizontally" is the scrollable parent element... it receives a className="scrollEnd" or scrollStart... do whatever with css
	*/ 
	componentDidMount() {
		// initial scroll
		if (this.refs.navItemSelected && this.refs.navItemSelected.offsetLeft > (this.refs.scrollHorizontally.offsetWidth-this.refs.navItemSelected.offsetWidth)) {
			this.refs.scrollHorizontally.scrollLeft = this.refs.navItemSelected.offsetLeft;
		}
		// on scroll
		this.scrollHorizontallyEnter(this.refs.scrollHorizontally);
		this.refs.scrollHorizontally.addEventListener("scroll", this.scrollHorizontallyEnter);
	}
	componentWillUnmount() {
		this.refs.scrollHorizontally.removeEventListener("scroll", this.scrollHorizontallyEnter);
	}
	scrollHorizontallyEnter = (element)=>{
		// container element is target
		if (element.target) {
			// so we can pass to this the element or the parent object of the element
			element = element.target;
		}
		// to show tip only when at the top... 
		element.classList.add('scrollLeft');
		element.classList.add('scrollRight');
		if (element.scrollLeft < 10) {
			element.classList.remove('scrollLeft');
		}
		if ((element.scrollLeft + element.offsetWidth + 11) > element.scrollWidth) {
			element.classList.remove('scrollRight');
		}
	}


	render() {

		// start > nav1 nav2 shared code
		const { router } = this.props;
		let pathnameSecondSlash = router.location.pathname.substr(1).indexOf('/') + 1;
		const pathname = router.location.pathname;
		const pathnameFirst = pathnameSecondSlash ? pathname.substring(0, pathnameSecondSlash) : pathname;
		const PagesLinks = [];
		for (let i = 0; i < window.routes.length; i++) {
			if (!window.routes[i]) {
				continue;
			}
			const pageLink = window.routes[i];
			const pageTabs = pageLink.children;
			const Nav1Tabs = [];
			let pagelinkSecondSlash = pageLink.url.substr(1).indexOf('/') + 1;
			pageLink.urlFirst = pagelinkSecondSlash ? pageLink.url.substring(0, pagelinkSecondSlash) : pageLink.url;
			const isPageSelected = pathnameFirst === pageLink.urlFirst;
			// end < nav1 nav2 shared code

			// tabs
			if (pageTabs && pathnameFirst === pageLink.urlFirst) {
				for (let t = 0; t < pageTabs.length; t++) {
					if (pageTabs[t] && !pageTabs[t].hidden) {
						let tab = pageTabs[t];
						// ok
						Nav1Tabs.push(
							<div key={ "Nav1Tabs"+t } className={ "tab link" + ((pathname === tab.url || (t===0 && pathname === pageLink.url)) ? ' selected' : '') } onClick={function(){ router.push( tab.url ) }}>
								{tab.tabTitle || tab.title}
							</div>
						);
					}
				}
			}

			// main nav
			const haveTabs = Nav1Tabs.length>1;
			PagesLinks.push(
				<div key={pageLink.urlFirst} ref={(isPageSelected ? 'navItemSelected' : '')} className={ 'navItem link' + (isPageSelected ? ' selected' : '') + (haveTabs ? ' withTabs' : '') } onClick={ (e)=>{ if (!haveTabs){ router.push(pageLink.url); } e.preventDefault(); } }>
					<div className="navItemIcon">
						{pageLink.icon}
					</div>
					<div className="navItemTitle">

						<div className={ "link " + ((isPageSelected) && " navItemTitleFirst") }>
							{pageLink.tabTitle || pageLink.title}
						</div>

						{haveTabs &&
							<div className="nav1Tabs">
								{Nav1Tabs}
							</div>
						}

					</div>
				</div>
			);

		};

		// ok
		return (
			<div ref="scrollHorizontally" className="navItems">
				<div className="navItem fixToTop" ref={(e)=>{ this.e = e; }} onClick={()=>{ $(this.refs.scrollHorizontally).animate({ scrollLeft: 0}, 666); }}>
					<div className="navItemIcon"><span className="utfIcon">&#9664;</span></div>
					<div className="navItemTitle">&nbsp;</div>
				</div>
				<div className="navItem fixToTop" ref={(e)=>{ this.e = e; }} onClick={()=>{ $(this.refs.scrollHorizontally).animate({ scrollLeft: $(this.refs.scrollHorizontally).prop("scrollWidth")}, 666); }}>
					<div className="navItemIcon"><span className="utfIcon">&#9658;</span></div>
					<div className="navItemTitle">&nbsp;</div>
				</div>
				{PagesLinks}
			</div>
		)
	}
}

export default Pages;


