import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';
import * as userActions from '@src/states/user/actions';

import { LuxulForm, LuxulInput, validations } from '@src/components/form/LuxulForm';

class FormLogin extends React.Component {

	constructor() {
		super();
		this.state = {};
	}

	componentDidMount() {
		const { dispatch } = this.props;
		// always log out if on this page
		dispatch( layoutActions.message({}) );
		dispatch( userActions.logout({}) )
		.catch((err)=>{ 
			dispatch(layoutActions.message(err));
		});
	}

	render() {

		var { dispatch, router } = this.props;

		var formSubmit = (promise) => {
			dispatch(layoutActions.message({}));
			promise
			.then((formData)=>{
				
				if (this.state.luxulFormValues.username === "admin" && this.state.luxulFormValues.password === "admin") {
					dispatch(layoutActions.message({status:"success", message:"Authenticated"}));
					router.push("/");
				} else {
					dispatch(layoutActions.message({status:"error", message:"Wrong username or password"}));
				}

			})
			.catch((err)=>{
				
				console.error("Error: "+err);

			});
		};

		return (
						
			<LuxulForm 
			stateScope={this} 
			onSubmit={formSubmit}
			>
				
				<div className="formSection">
					<fieldset>
						<label>Username:</label>
						<LuxulInput stateScope={this} name="username" type="text" validations={[validations.required]} />
					</fieldset>
					<fieldset>
						<label>Password:</label>
						<LuxulInput stateScope={this} name="password" type="password" validations={[validations.required]} />
					</fieldset>
				</div>

				<div className="formSubmit">
					<span ref="buttons" className="right">
						{/* Submit */}
						<button className={"mm " +((this.state.luxulFormInvalid===undefined || !this.state.luxulFormInvalid.length) ? " primary" : "")} type="submit"><b>Enter</b></button>
					</span>
				</div>

			</LuxulForm>
		)
	}

};

const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)( FormLogin );



