
const actions = {

	sideBox: function(sideBox) { 
		return {
			type: 'layout/sideBox',
			data: sideBox
		};
	},

	popupBox: function(popupBox) { 
		return {
			type: 'layout/popupBox',
			data: popupBox
		};
	},

	reboot: function(reboot) { 
		return {
			type: 'layout/reboot',
			data: true
		};
	},
	rebootX: function(rebootX) { 
		return {
			type: 'layout/rebootX',
			data: true
		};
	},

	notification: function(notification) { 
		return {
			type: 'layout/notification',
			data: true
		};
	},
	notificationX: function(notificationX) { 
		return {
			type: 'layout/notificationX',
			data: true
		};
	},

	message: function(message) { 
		return {
			type: 'layout/message',
			data: message
		};
	},
	messageX: function() { 
		return {
			type: 'layout/messageX',
			data: {}
		};
	},
	messageTimeout: function(extendTime) { 
		return {
			type: 'layout/messageTimeout',
			data: {extendTime:extendTime}
		};
	},
	messageExtend: function(extendTime) { 
		return {
			type: 'layout/messageExtend',
			data: {extendTime:extendTime}
		};
	}

};

module.exports = actions;


