import React from 'react';
import { connect } from 'react-redux';

import Auth from '@src/components/user/Auth';
import Nav1 from '@src/components/layout/components/Nav1';
import Nav2 from '@src/components/layout/components/Nav2';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/Pages.scss';

class Pages extends React.PureComponent {

	componentDidMount() {
		// whenever any page/route changes, hide message
		this.props.dispatch( layoutActions.message({}) );
	}

	render() {
		const { router } = this.props;

		// ok
		return (
			<div className="pages nav_left">

				<Auth router={router}/>

				<Nav1 router={router} rerender={router.location.pathname}  />

				<Nav2 router={router} rerender={router.location.pathname} />

			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {};
};
export default connect(mapStateToProps)(Pages);


