import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/Messages.scss';

class Messages extends React.PureComponent {

	render() {
		const { layout, dispatch } = this.props;

		// no message
		if (!layout.message.message || !layout.message.active) {
			return(<div />);
		}

		// validate
		if (layout.message.message === "Unexpected token < in JSON at position 0") {
			layout.message.status = "build-app-preview";
			layout.message.message = "You are previewing the production version of the app. However,";
			layout.message.support = ["It only functions when run from the Luxul hardware. What you are seeing is only a demo experience used to check that elements and modules are compiled correctly.","To see the full version, please go and tap this guy on the shoulder:","Paul Shorey - <a href='mailto:pshorey@luxul.com'>pshorey@luxul.com</a>"];
		}

		// login
		// if (layout.message.message ==="Session expired") {
		// 	window.location.reload();
		// }

		// log
		if (layout.message.status === 0) {
			window.log("success", layout.message);
		} else if (layout.message.status === undefined) {
			window.log("log", layout.message);
		} else {
			window.log("error", layout.message);
		}

		// style
		const theme = layout.message.status===undefined ? "" : layout.message.status===0 ? "theme_success" : "theme_error";

		// timeout
		// if expires in next 5 seconds (meaning it has not been extended), only expires if its not an error (errors stay up until closed or navigated away from)
		if (layout.message.opened && (!layout.message.status || layout.message.status==="0") && layout.message.expires <= (Date.now() + 5000)) {
			setTimeout(()=>{ 
				dispatch( layoutActions.messageTimeout() ); }, 
				5000
			);
		}

		// content
		let MessageSupportDetails;
		if (layout.message.supportDetails) {
			MessageSupportDetails = [];
			if (typeof layout.message.supportDetails === "object") {
				for (let i in layout.message.supportDetails) {
					if (layout.message.supportDetails[i]) {
						MessageSupportDetails.push(<div key={"supDeets"+i}>{i}: {layout.message.supportDetails[i]}</div>);
					}
				}
			}
		}
		let MessageSupport;
		if (layout.message.support) {
			MessageSupport = [];
			if (typeof layout.message.support === "object") {
				let bullet = 1;
				for (let i in layout.message.support) {
					if (layout.message.support[i]) {
						MessageSupport.push(<div key={"sup"+i}><span dangerouslySetInnerHTML={{__html: layout.message.support[i]}} /> {(MessageSupportDetails && bullet===Object.keys(layout.message.support).length) ? <span className="more">...&#9662;</span> : ""}</div>);
					}
					bullet++;
				}
			}
		}
		let MessageTitle = [];
		MessageTitle.push(<div key="messtitle">{(layout.message.title || layout.message.message)} {(MessageSupport ? <span className="more">...&#9662;</span> : "")}</div>);

		// open
		return (
			<div className="top messages_container">
			<div className="messages_content">
			<div className="messages_left nav_left">
			</div>
			<div className="messages_right">

				<div className={ "message" + ( layout.message.opened ? ' opened' : '' ) + ( theme ? " "+theme : "" )} onMouseEnter={()=>{ dispatch(layoutActions.messageExtend(60000)); setTimeout(()=>{ dispatch(layoutActions.messageTimeout()); }, 60000); }}>

					<div className="message_content">
						
						<div className="messageX" onClick={()=>{ dispatch(layoutActions.messageX()); }}>
							<span className="fontIcon icon-cross" title="hide message" />
						</div>

						<div className="message_text messageTitle">
							{MessageTitle}

							{MessageSupport && 
							<div className="message_text messageSupport">
								{MessageSupport}

								{MessageSupportDetails &&
								<div className="message_text messageSupportDetails">
									{MessageSupportDetails}
								</div>
								}
							</div>
							}
						</div>

					</div>
					
				</div>
			</div>
			</div>
			</div>
		)
	};
}

const mapStateToProps = (state) => {
    return {
    	user: state.user,
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Messages);


