import React from 'react';
import { connect } from 'react-redux';

import * as userActions from '@src/states/user/actions';
import * as layoutActions from '@src/states/layout/actions';

class Auth extends React.PureComponent {

	componentWillMount() {
		const { dispatch, router } = this.props;
		/*
			auto login
		*/
		if (!this.props.user.loggedIn && window.localStorage.ubusUsername) {
			dispatch(userActions.login({}))
			.then((action)=>{
			})
			.catch((err)=>{ 
				router.push('/login');
				dispatch(layoutActions.message({message:"Automatic login failed. Please check that your device is connected and powered on.", status:1}));
			});
		}
		/*
			manual login
		*/
		else if (!this.props.user.loggedIn) {
			router.push('/login');
		}
	};

	render() {
		return (<a name="auth" />)
	}
}

const mapStateToProps = (state) => {
    return {
    	user: state.user
    };
};
export default connect(mapStateToProps)(Auth);


