import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import Messages from '@src/components/layout/Messages';

import '@src/components/layout/Top.scss';

class Top extends React.PureComponent {

	render() {
		const { dispatch, router } = this.props;

		const DeviceInfo = {};
		DeviceInfo.title = 'New Luxul Product';
		DeviceInfo.theme = 'global';
		DeviceInfo.content = 
			<div className="form">
				<p>Model #: LUX-4500V1</p>
				<p>Firmware #: SDJ1232032</p>
			</div>
		;

		return (
			<div className="top">
				<Messages router={router} />

				<span className="nav_left navItems navHeading">
					<span className="navItem navLogo link">
						<img className="" src="/branding/logo/logo_white_simplyconnected.svg" alt="LUXUL" onClick={()=>{ /*router.push("/login")*/ }} />
					</span>
				</span>
				
				<span className="navItems navHeading">
					<span className="navItem link" onClick={()=>{ if (DeviceInfo && DeviceInfo.content) { dispatch( layoutActions.sideBox(DeviceInfo) ) }}}>
						<span className="navItemTitle" style={{textIndent:"-1.5px"}}><span className="fontIcon icon-carat_down" style={{paddingTop:"0",paddingLeft:"0"}}></span> <span>{window.device.info.model}</span></span>
						<span className="navItemText" style={{textIndent:"-1px"}}>Firmware {window.device.info.firmware} {window.device.info.app_version}</span>
					</span>
				</span>
				
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Top);


