import $ from "jquery";

import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';
import * as userActions from '@src/states/user/actions';

import { LuxulForm, LuxulInput, LuxulSelect, LuxulToggle, LuxulHint, validations } from '@src/components/form/LuxulForm';

class FormNServer extends React.Component {

constructor() {
	super();
	this.state = {
		luxulFormValues: {
			connection_type: "DHCP",
			radio13: false,
			radio24: true,
			radio50: false,
			radio58: false
		},
		luxulFormButtons: {
			submit: "Save Changes",
			reset: true,
			invalid: true,
			na: true,
			submitting: true,
			success: true,
			error: true
		}
	};
}

	render() {
		// if (!Object.keys(this.state.luxulFormValues).length) {
		// 	setTimeout(()=>{
		// 		this.setState({
		// 			luxulFormValues: {
		// 				connection_type: "DHCP",
		// 				radio13: false,
		// 				radio24: true,
		// 				radio50: false,
		// 				radio58: false
		// 			}
		// 		});
		// 	},1000);
		// }

		var { dispatch, router } = this.props;

		var handleSubmit = (promise) => {
			dispatch(layoutActions.message({}));
			promise
			.then(()=>{
				// Validation Success
				console.info("Form validation successful");
				this.setState({luxulFormSubmitting:true});

				dispatch(userActions.login(this.state.luxulFormValues))
				.then((action)=>{
					// Action Success
					this.setState({luxulFormSubmitting:false});
					this.setState({luxulFormSuccess:true});
					// Action ▸
					router.push('/');
				})
				.catch((err="")=>{ 
					// Action Fail
					this.setState({luxulFormSubmitting:false});
					this.setState({luxulFormError:true});
					// Action ▸
					dispatch(layoutActions.message(err));
				});
			})
			.catch((err)=>{
				// Validation Fail
			});
		};

		return (
						
			<LuxulForm 
			stateScope={this} 
			onSubmit={handleSubmit}
			>
				

				<div className="formSection">
					<h4>Connection Type: <LuxulHint stateScope={this} title="Connection Type" description={<span><p>Select the type of ISP connection being used for the network.</p><p>DHCP - The most common method for connecting to an ISP. If the ISP gives the router an IP address from their DHCP server, use this option.</p><p>PPPoE - Many DSL-based ISPs use PPoE (Point-to-Point Protocol over Ethernet) to establish Internet connections.</p><p>Static IP - If the ISP has assigned a static IP address to this account, select this option.</p></span>} /></h4>
					<fieldset>
						<LuxulSelect stateScope={this} name="connection_type" options={new Map([["Choose...",""], ["Static IP","Static IP"], ["PPPoE","PPPoE"], ["DHCP","DHCP"]])} validations={[validations.required]} />
					</fieldset>
				</div>


				<div className="formSection">
					<h4>Enable Frequencies:  <LuxulHint stateScope={this} title="Independent Toggle Fields Group" description={<span><p><ul><li>Checkboxes in this group are completely separate from one another.</li><li>They must be validated separately.</li><li>You cannot validate this as a group, like, to check if one of the options is selected, doesn't matter which one. It does matter which one is selected, because validation must be put on each separate toggle switch.</li></ul></p></span>} /></h4>
					<fieldset>
						<div className="boxed">
							<LuxulToggle stateScope={this} name="radio24" labelOn="2.4Ghz" validations={[validations.required]} />
							<LuxulToggle stateScope={this} name="radio50" labelOn="5.0Ghz" />
						</div>
					</fieldset>
				</div>


				{/*<div className="formSection">
					<h4>Group of Frequencies:  <LuxulHint stateScope={this} title="Group of Frequencies" description={<span><p>Checkboxes in this group are validated as a group. So, you can make this group "required", and if any of the toggle switches are on, it will pass the validation.</p></span>} /></h4>
					<fieldset>
						<div className="boxed">
							<LuxulToggle stateScope={this} name="radio24B" labelOn="2.4Ghz" validations={[validations.required]} />
							<LuxulToggle stateScope={this} name="radio50B" labelOn="5.0Ghz" />
						</div>
					</fieldset>
				</div>*/}


				{this.state.luxulFormValues.connection_type==="DHCP" && 
					<div className="formSection">
						<h4>DHCP:</h4>
						<fieldset>
							<label>IPv4 Address: <LuxulHint stateScope={this} title="DHCP Tips" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
							<LuxulInput stateScope={this} name="DHCP_input" type="text" placeholder="0.0.0.0" />
						</fieldset>
					</div>
				}


				{this.state.luxulFormValues.connection_type==="Static IP" && 
					<div className="formSection">
						<h4>Static IP:</h4>
						<fieldset>
							<label>IPv4 Address: <LuxulHint stateScope={this} title="Static IP" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
							<LuxulInput stateScope={this} name="static_ip" type="text" placeholder="0.0.0.0" />
						</fieldset>
					</div>
				}

				
				<div className="formSection">
					<fieldset>
						<label>Etc 1: <LuxulHint stateScope={this} title="Etc 1 Tips" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
						<LuxulInput stateScope={this} name="etc1_input" type="text" placeholder="0.0.0.0" />
					</fieldset>
					<fieldset>
						<label>Etc 2: <LuxulHint stateScope={this} title="Etc 2 Tips" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
						<LuxulInput stateScope={this} name="etc2_input" type="text" placeholder="0.0.0.0" />
					</fieldset>
					<fieldset>
						<label>Etc 3: <LuxulHint stateScope={this} title="Etc 3 Tips" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
						<LuxulInput stateScope={this} name="etc3_input" type="text" placeholder="0.0.0.0" />
					</fieldset>
					<fieldset>
						<label>Etc 4: <LuxulHint stateScope={this} title="Etc 4 Tips" description={<span><p>Some helpful tips about this field...</p></span>} /></label>
						<LuxulInput stateScope={this} name="etc4_input" type="text" placeholder="0.0.0.0" validations={[validations.required]} />
					</fieldset>
				</div>


			</LuxulForm>
		)
	}

};

const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)( FormNServer );


