import React from 'react';

import { LuxulForm, LuxulSelect, validations } from '@src/components/form/LuxulForm';

class SelectStuff extends React.Component {

constructor() {
	super();
	this.state = {
		luxulFormValues: {
			initial_choice: ""
		}
	};
}

	render() {

		var handleSubmit = (promise) => {
			// form submit attempted. Don't do anything.
		};

		return (
						
			<LuxulForm 
			stateScope={this} 
			onSubmit={handleSubmit}
			>

				<div className="formSection">
					<h4>LuxulSelect component:</h4>
					<fieldset>
						<label>Choose one:</label>
						<LuxulSelect stateScope={this} name="initial_choice"  options={new Map([["Select...",""], ["Selection 1","Selection 1"], ["Selection 2","Selection 2"], ["Selection 3","Selection 3"]])} validations={[validations.required]} />
					</fieldset>
				</div>

				{this.state.luxulFormValues.initial_choice==="Selection 1" && 
					<div className="formSection">
						<h4>Cool! You chose the first option.</h4>
						<fieldset>
							<label>Now you can choose something more specific:</label>
							<LuxulSelect stateScope={this} name="secondary_choice"  options={new Map([["Select...",""], ["1 part A","1 part A"], ["1 part B","1 part B"], ["1 part C","1 part C"]])} />
						</fieldset>
					</div>
				}
				{this.state.luxulFormValues.initial_choice==="Selection 2" && 
					<div className="formSection">
						<h4>You chose the second option.</h4>
						<fieldset>
							<label>Now if you want, try choosing a more specific:</label>
							<LuxulSelect stateScope={this} name="secondary_choice"  options={new Map([["Select...",""], ["2 part A","2 part A"], ["2 part B","2 part B"], ["2 part C","2 part C"]])} />
						</fieldset>
					</div>
				}
				{this.state.luxulFormValues.initial_choice==="Selection 3" && 
					<div className="formSection">
						<h4>You chose the third and last option...</h4>
						<fieldset>
							<label>Now optionally, lets make a secondary selection to narrow it down even more:</label>
							<LuxulSelect stateScope={this} name="secondary_choice"  options={new Map([["Select...",""], ["3 part A","3 part A"], ["3 part B","3 part B"], ["3 part C","3 part C"]])} />
						</fieldset>
					</div>
				}

			</LuxulForm>
		)
	}

};

export default SelectStuff;


