/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import LoginForm from '@src/components/user/FormLogin';
import "@src/pages/user/PageLogin.scss";


/*
	form
*/
class PageComponent extends React.Component {


	render() {
		const sides = [
			{
				title: <span>Login Help</span>,
				icon: <span className="fontIcon icon-pk-question" title="Page Options"></span>,
				content: 
					<div className="form">
						<p>Original factory settings are:<br />username = "root"<br />password = "aw3AW#"</p>
						<p>&nbsp;</p>
						<p>If you have changed and forgotten the username, password, or IP address at which to access this screen, you must physically reset your device by pressing the "reset" button in the back of the unit.</p>
					</div>
			}
		];

		return (
			<Layout router={this.props.router} route={this.props.route} pages={false}>
				
				<Box box={{title:this.props.route.page.title,id:"boxLogin"}} sides={sides}>
					
					<LoginForm router={this.props.router} />

				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


