import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/Top.scss';

class Notifications extends React.PureComponent {

	render() {
		const { dispatch, layout } = this.props;

		const Reboot = 
			(layout.suggestions.reboot)
			?
			<span key="reboot" className={"navItem button reboot"} onClick={()=>{ dispatch( layoutActions.rebootX()) }}>
				<button className="mm secondary" type="button">
					<b>Reboot</b>
				</button>
			</span>
			:
			""
		;
		const Notification = 
			(layout.notification && layout.notification.message)
			?
			<span key={"notification-"+0} className={"navItem link color-notice"} onClick={()=>{ dispatch( layoutActions.rebootX()) }}>
				<span className="navItemTitle" style={{textIndent:"-1.5px"}}>
					<span className="fontIcon icon-pk-important"></span> {layout.notification.message}
				</span>
				<span className="navItemText" style={{textIndent:"-1px"}}>{layout.notification.support}</span>
			</span>
			:
			""
		;

		return (
			
			(Notification || Reboot)
			?
			<div className="top notifications">
				<span className="nav_right navItems navHeading">
					{Notification}
					{Reboot}
				</span>
			</div>
			:
			<div />
			
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Notifications);


