import {RPCD} from '@src/middleware/rpcd';

export const startSession = function() {
    const data = {
        ubusObject: 'session',
        ubusMethod: 'logout',
        ubusArgs: {},
        onSuccess: "user/logout",
        onFailure: "user/logoutFail"
    };
    return {
        type: 'user/submitting',
        data: {},
        [RPCD]: data
    }
};

export const login = function(formData) {
    var { username, password } = formData;
    // from session
    if (!username && window.localStorage.ubusUsername) {
        username = window.localStorage.ubusUsername;
        password = window.localStorage.ubusPassword;
    }
    // n/a
    if (!username) {
        console.error("login username/password not provided");
    }
    const data = {
        ubusObject: 'session',
        ubusMethod: 'login',
        ubusArgs: {
            "username": username,
            "password": password,
        },
        onSuccess: "user/login",
        onFailure: "user/loginFail"
    };
    return {
        type: 'user/submitting',
        data: formData,
        [RPCD]: data
    }
}

export const logout = function() {
    const data = {
        ubusObject: 'session',
        ubusMethod: 'logout',
        ubusArgs: {},
        onSuccess: "user/logout",
        onFailure: "user/logoutFail"
    };
    return {
        type: 'user/submitting',
        data: {},
        [RPCD]: data
    }
};




