const initialState = {
	switch0: {on:true},
	switch1: {on:true},
	switch2: {on:false},
	switch3: {on:false},
	switch4: {on:false},
	switch5: {on:false},
	switch6: {on:false},
	switch7: {on:false}
};

export default function userReducer(oldState = initialState, action = {}) {
	switch (action.type) {


		case 'switches/getAll':
			{
				// validate
				if (!action.data) {
					return oldState;
				}

				// state
				let state = Object.assign({}, oldState);
				// state.switchX
				if (action.data.values) {
					state.switch0.on = action.data.values.switch0;
					state.switch1.on = action.data.values.switch1;
					state.switch2.on = action.data.values.switch2;
					state.switch3.on = action.data.values.switch3;
					state.switch4.on = action.data.values.switch4;
					state.switch5.on = action.data.values.switch5;
					state.switch6.on = action.data.values.switch6;
					state.switch7.on = action.data.values.switch7;
				}

				// =
				window.log("action success", 'action @ switches/getAll ', ["prev state:", oldState], ["action:", action], ["next state:", state]);
				return state;
			}


		case 'switches/submitting':
			{
				let state = Object.assign({}, oldState);
				for (let s in action.data) {
					if (action.data[s].on !== undefined) {
						state[s].submitting = true;
					}
				}
				window.log("action", 'action @ switches/submitting ', ["prev state:", oldState], ["action:", action], ["next state:", state]);
				return state;
			}
		case 'switches/setSwitch':
			{
				let state = Object.assign({}, oldState);
				for (let s in action.data) {
					if (action.data[s].on !== undefined) {
						state[s].submitting = false;
						state[s].on = action.data[s].on;
					}
				}
				window.log("action success", 'action @ switches/setSwitch ', ["prev state:", oldState], ["action:", action], ["next state:", state]);
				return state;
			}
		case 'switches/setSwitch_error':
			{
				let state = Object.assign({}, oldState);
				// Mr Bill: "Oh Noooo!"
				// for (let s in action.data) {
				// 	if (action.data[s].on !== undefined) {
				// 		state[s].error = true;
				// 	}
				// }
				window.log("action fail", 'action @ switches/setSwitch_error ', ["prev state:", oldState], ["action:", action], ["next state:", state]);
				return state;
			}
		case 'switches/setSwitch_unError':
			{
				let state = Object.assign({}, oldState);
				for (let s in action.data) {
					if (action.data[s].on !== undefined) {
						state[s].error = false;
						state[s].submitting = false;
					}
				}
				window.log("action", 'action @ switches/setSwitch_unError ', ["prev state:", oldState], ["action:", action], ["next state:", state]);
				return state;
			}



		default:
			{
				return oldState;
			}
	}
}



