/*
	helpers
*/
const jsonStringify = function (obj) {
    var objKeys = Object.keys(obj);
    var keyValueArray = new Array();
    for (var i = 0; i < objKeys.length; i++) {
        var keyValueString = '"' + objKeys[i] + '":';
        var objValue = obj[objKeys[i]];
        keyValueString = (typeof objValue == "string") ? 
            keyValueString = keyValueString + '"' + objValue + '"' : 
            keyValueString = keyValueString + jsonStringify(objValue);
        keyValueArray.push(keyValueString);
    }
    return "{" + keyValueArray.join(",") + "}";
}
const LS = function(name, value){
	if (value===undefined) {
		value = window.localStorage[name];
		if (value.indexOf(":\"")>-1) {
			value = JSON.parse(value);
		}
	} else {
		if (typeof(value) === "object") {
			value = jsonStringify(value);
		}
		window.localStorage[name] = value;
	}
	return value;
};


/*
	meta
*/
// original taken from ubusmsg.h: http://git.openwrt.org/?p=project/ubus.git;a=blob_plain;f=ubusmsg.h;hb=refs/heads/master
import isPlainObject from 'lodash/isPlainObject';
export const RPCD = '@@RPCD';
// status and stuff
export const UBUS_STATUS_OK = 0;
export const UBUS_STATUS_INVALID_COMMAND = 1;
export const UBUS_STATUS_INVALID_ARGUMENT = 2;
export const UBUS_STATUS_METHOD_NOT_FOUND = 3;
export const UBUS_STATUS_NOT_FOUND = 4;
export const UBUS_STATUS_NO_DATA = 5;
export const UBUS_STATUS_PERMISSION_DENIED = 6;
export const UBUS_STATUS_TIMEOUT = 7;
export const UBUS_STATUS_NOT_SUPPORTED = 8;
export const UBUS_STATUS_UNKNOWN_ERROR = 9;
export const UBUS_STATUS_CONNECTION_FAILED = 10;
export class UbusError extends Error {
	constructor(code, message) {s
		if (typeof(message) === 'undefined') {
			switch (code) {
				case UBUS_STATUS_INVALID_COMMAND:
					message = 'ubus error: invalid command';
					break;
				case UBUS_STATUS_INVALID_ARGUMENT:
					message = 'ubus error: invalid argument';
					break;
				case UBUS_STATUS_PERMISSION_DENIED:
					message = 'ubus error: permission denied';
					break;
				case UBUS_STATUS_METHOD_NOT_FOUND:
					message = 'ubus error: method not found';
					break;
				case UBUS_STATUS_NOT_FOUND:
					message = 'ubus error: not found';
					break;
				case UBUS_STATUS_NO_DATA:
					message = 'ubus error: no data';
					break;
				case UBUS_STATUS_TIMEOUT:
					message = 'ubus error: timeout';
					break;
				case UBUS_STATUS_NOT_SUPPORTED:
					message = 'ubus error: not supported';
					break;
				case UBUS_STATUS_UNKNOWN_ERROR:
					message = 'ubus error: unknown error';
					break;
				case UBUS_STATUS_CONNECTION_FAILED:
					message = 'ubus error: connection failed';
					break;
				default:
					message = 'ubus error: ' + code;
			}
		}
		super(message);
		this.message = message;
		this.code = code;
	}
}


/*
	authentication
*/
var ubusUsername = ""; 
var ubusPassword = "";
var ubusSession = "";
var ubusSessionExpire = "";

const rpcdRequestLogout = ()=>{
	ubusUsername = window.localStorage.ubusUsername = ""; 
	ubusPassword = window.localStorage.ubusPassword = "";
	ubusSession = window.localStorage.ubusSession = "";
	ubusSessionExpire = window.localStorage.ubusSessionExpire = "";
	// no further action needed, just do next(action)
}
const rpcdRequestLogin = async (username=window.localStorage.ubusUsername||ubusUsername, password=window.localStorage.ubusPassword||ubusPassword) => {
	const result = await rpcdRequest("session", "login", {"username": username, "password": password});
	return result;
	// window.localStorage vars have been set in rcpdRequest(...)
};


/*
	requests
*/
const rpcdRequest = async (ubusObject, ubusMethod, ubusArgs = {}) => {

	const make_errorMessage = (params)=>{

		var err =  {
			status: (params.status || "error"),
			message: (params.message || "Request failed"),
			support: (params.support || "")
		}

		// error details
		if (params.supportDetails) {

			// support details (if needed, add to err object)
			var supportDetails = {};
			supportDetails["Device"] = window.appVars.device_model;
			supportDetails["Firmware"] = window.appVars.device_firmware;
			supportDetails["App version"] = window.appVars.app_version;
			for (var i in params.supportDetails) {
				supportDetails[i] = params.supportDetails[i];
			};
			supportDetails["ubusMethod"] = ubusMethod;
			supportDetails["ubusArgs"] = JSON.stringify(ubusArgs);
			delete params.supportDetails;

			// error code
			if (supportDetails.code) {
				switch (supportDetails.code) {
					case "ubus-json--32002" :
						if (window.location.href.toString().split(window.location.host)[1]==="/login") {
							return {supportDetails:["Session expired, but on login page, so suppressing this error"]};
						}
						err.status = "Session expired";
						err.message = "Session expired";
						err.support = [
							"The connection to your device has been interrupted. This usually happens if you continue using this app after your computer has been asleep. It is also possible that your device was restarted since you last logged in.",
							"<a href='/login'>Please click here to go back and login again.</a>"
						];
						supportDetails = undefined;
					break;
					default:
						err.support = [
							"Please restart the device and your browser.",
							"Please update your device to the latest firmware.", 
							"If that doesn't work, please contact support with the following info:"
						];
						err.supportDetails = supportDetails;
					break;
				}
			}

		}
		return err;

	};

	// if no session, login
	let session = window.localStorage.ubusSession || "00000000000000000000000000000000";
	if (ubusObject === "session" && ubusMethod === "login") {
		session = "00000000000000000000000000000000";
	}

	/*
		request
	*/
	// validate
	if (ubusArgs.values) {
		ubusArgs.values = Object.assign({},ubusArgs.values);
		for(let u in ubusArgs.values) { 
			if (ubusArgs.values[u]===true) {
				ubusArgs.values[u]="true";
			}
			else 
			if (ubusArgs.values[u]===false) {
				ubusArgs.values[u]="false";
			}
		}
	}
	// set
	const postData = {
		method: 'POST',
		body: JSON.stringify({
			"jsonrpc": "2.0",
			"id": "1",
			"method": "call",
			"params": [
				session,
				ubusObject,
				ubusMethod,
				ubusArgs,
			]
		})
	};
	
	/*
		response
	*/
	let response;
	try {
		response = await fetch('/ubus', postData);
		// this is probably same as catch(err) below, but not sure, could mean different things - needs more investigation before merging by throwing new Error()
		if (!response.ok) {
			var msg = make_errorMessage({message:"Could not connect to device", support:["Please check that the device is powered on and ethernet cord is connected.", "It takes about 2 minutes to start up after being unplugged."], status:"ubus !response.ok"});
			console.error("RPCD Middleware error: ", msg);
			return msg;
		}
	} catch(err) {
		var msg = make_errorMessage({message:"Could not connect to device", support:["Please check that the device is powered on and ethernet cord is connected.", "It takes about 2 minutes to start up after being unplugged."], status:"ubus response catch(err)"});
		console.error("RPCD Middleware error: ", msg);
		return msg;
	}

	/* 
		validate
		json.error[0] ... error code
		json.error[1] ... error message
		json.result[0] ... result status (0 for no problems)
		json.result[1] ... result data
	*/
	var json = await response.json();
	// fail
	if (json.error) {
		var msg = make_errorMessage({message:(json.error.message || json.error[1]), supportDetails:{"code":"ubus-json-"+(json.error.code || json.error[0])}});
		console.error("RPCD Middleware error: ", msg);
		return msg;
	} else if (json.result[0] !== 0) {
		switch (json.result[0]) {
			case 6:
				var msg = make_errorMessage({message:"Wrong username or password"});
				console.error("RPCD Middleware error: ", msg);
				return msg;
			break;
			case 4:
				var msg = make_errorMessage({supportDetails:{"code":"ubus-"+json.result[0], "documentation":"'ubusArgs.config' value does not match that of on device"}});
				console.error("RPCD Middleware error: ", msg);
				return msg;
			break;
			case 3:
				var msg = make_errorMessage({supportDetails:{"code":"ubus-"+json.result[0], "documentation":"unable to use 'ubusMethod'"}});
				console.error("RPCD Middleware error: ", msg);
				return msg;
			break;
			default:
				var msg = make_errorMessage({supportDetails:{"code": "ubus-"+json.result[0]}});
				console.error("RPCD Middleware error: ", msg);
				return msg;
			break;
		}
	// success
	} else {
		var jsonResultData = json.result[1];
		if (ubusMethod==="get") {
			if (jsonResultData.values) {
				for(let u in jsonResultData.values) { 
					if (jsonResultData.values[u]==="true") {
						jsonResultData.values[u]=true;
					}
					else 
					if (jsonResultData.values[u]==="false") {
						jsonResultData.values[u]=false;
					}
				}
			}
		}
	}

	/*
		commit
	*/
	if (ubusMethod==='set' && (json.result && json.result[0]===0 && json.result[1]===undefined) ) {
		const commit = await rpcdRequest(ubusObject, "commit", ubusArgs);
	}

	/*
		session
	*/
	if (ubusObject === 'session' && ubusMethod === 'login') {
		if (json.result && json.result[1] && json.result[1].ubus_rpc_session) {
			const { ubus_rpc_session: session, expires } = json.result[1];
			ubusSession = window.localStorage.ubusSession = session;
			ubusSessionExpire = window.localStorage.ubusSessionExpire = new Date(Date.now() + (expires*1000));
			ubusUsername = window.localStorage.ubusUsername = ubusArgs.username;
			ubusPassword = window.localStorage.ubusPassword = ubusArgs.password;
		}
	}

	/*
		return to middleware
	*/
	return json.result[1];
};















/*
	rpcdMiddleware(store)(next)(action)
*/
export const rpcdMiddleware = store => next => async action => {
	/*
		validate
	*/
	// no middleware
	if (!isPlainObject(action) || !action.hasOwnProperty(RPCD)) {
		return next(action);
	}
	// yes middleware
	next(action);
	const call = action[RPCD];
	const { ubusObject, ubusMethod, ubusArgs, onRequest, onSuccess, onFailure } = call;
	// no data
	if (!ubusObject || !ubusMethod || !ubusArgs) {
		throw new Error('!ubusObject || !ubusMethod || !ubusArgs');
	}


	/*
		login / logout
	*/
	if (ubusObject === 'session' && ubusMethod === 'logout') {
		if (window.localStorage.ubusUsername||ubusUsername) {
			rpcdRequestLogout();
		} 
		if (onSuccess) {
			action.type = onSuccess;
		}
		return next(action);
	}
	if (ubusObject === 'session' && ubusMethod === 'login') {
		// treat login as regular request - session variables are saved inside rpcdRequest()
	}


	/*
		request
	*/
	let resultData = await rpcdRequest(ubusObject, ubusMethod, ubusArgs);
	// expired
	if (resultData && resultData.status && resultData.status==="Session expired") {
		resultData = await rpcdRequestLogin();
		resultData = await rpcdRequest(ubusObject, ubusMethod, ubusArgs);
	}
	// failed
	if (resultData && resultData.status) {
		if (onFailure) {
			action.type = onFailure;
		}
		next(action);
		throw resultData;
	}
	// success
	if (onSuccess) {
		action.type = onSuccess;
		if (resultData && resultData.values) {
			action.data.values = resultData.values;
		}
	}
	return next(action);

};







