const initialState = {
	debug: false,
	loggedIn: true
};

export default function userReducer(oldState = initialState, action = {}) {
	switch (action.type) {

		case 'user/submitting':
		{
			let state = Object.assign({}, oldState);
			// action
			state.submitting = true;
			// ok
			window.log("action", 'action @ user/submitting', ["prev state:", oldState], ["action:", action], ["next state:", state]);
			return state;
		}
		case 'user/login':
		{
			let state = Object.assign({}, oldState);
			// action
			state.loggedIn = true;
			state.submitting = false;
			// ok
			window.log("action success", 'action @ user/login', ["prev state:", oldState], ["action:", action], ["next state:", state]);
			return state;
		}
		case 'user/loginFail': {
			let state = Object.assign({}, oldState);
			// action
			state.submitting = false;
			// ok
			window.log("action fail", 'action @ user/loginFail', ["prev state:", oldState], ["action:", action], ["next state:", state]);
			return state;
		}

		case 'user/logout': {
			let state = Object.assign({}, oldState);
			// action
			state.loggedIn = false;
			state.submitting = false;
			// ok
			window.log("action success", 'action @ user/logout', ["prev state:", oldState], ["action:", action], ["next state:", state]);
			return state;
		}
		case 'user/logoutFail': {
			let state = Object.assign({}, oldState);
			// action
			state.submitting = false;
			// ok
			window.log("action fail", 'action @ user/logoutFail', ["prev state:", oldState], ["action:", action], ["next state:", state]);
			return state;
		}

		default: {
			return oldState;
		}

	}
}



